import React, { useRef, useEffect, useState } from "react";
import TestChart from "../components/Forecast/charts/TestChart";
import { Grid, Segment, Statistic, Checkbox, Button } from "semantic-ui-react";
import RadChart from "../components/Forecast/charts/RadChart";
import axiosInstance from "../axios/axios";
import ExcelExport from "../components/Forecast/excelexport/ExcelExport";
import ExcelCosts from "../components/Forecast/excelexport/ExcelCosts";
import svgDownload from "../components/Forecast/svgDownload.js";

const reducer = (accumulator, currentValue) => accumulator + currentValue;

function ForecastScreen() {
  const radRef = useRef(null);
  const foreCastRef = useRef(null)

  useEffect(() => {
    console.log(foreCastRef.current)
    
  }, [foreCastRef])

  const [graphWidth, setGraphWidth] = useState(0);
  const [charges, setCharges] = useState([]);
  const [sumOfCharges, setSumOfCharges] = useState(0);
  const [balance, setBalance] = useState({});
  const [invoices, setInvoices] = useState([]);
  const [income, setIncome] = useState([]);
  const [incomeData, setIncomeData] = useState([]);
  const [aussicht, setAussicht] = useState(true)

  const queryInvoices = async () => {
    try {
      const invs = await axiosInstance.get("invoicesall/");      
      
      const res = await axiosInstance.get("projects/");
      setInvoices(invs.data.map(invoice => ({...invoice, project: res.data.find(project=>project.id === invoice.project)})));

    } catch (err) {
      console.log(err.response);
    }
  };

  const queryCharges = async () => {
    try {
      const res = await axiosInstance.get("forecast/charges");
      setCharges(res.data);
      setSumOfCharges(
        res.data
          .map((item) => parseFloat(item.amount_per_month))
          .reduce(reducer)
      );
    } catch (err) {
      console.log(err.response);
    }
  };

  const queryBalance = async () => {
    try {
      const res = await axiosInstance.get("forecast/balance");
      setBalance(res.data[0]);
    } catch (err) {
      console.log(err.response);
    }
  };

  useEffect(() => {
    setIncome(
      invoices
        .filter((item) => {
          return new Date(item.date_of_payment) >= new Date(balance.date);
        })
        .map((item) => ({
          ...item,
          amount: parseFloat(item.amount),
          date_of_payment: new Date(item.date_of_payment),
        }))
    );
  }, [balance, invoices]);

  useEffect(() => {

    const projectstatusfilter = aussicht ? [1,2,3,4,5] : [2,3,4,5]

    if (income.length != 0) {
      const data = new Array(12).fill().map((item, i) => {
        const date = new Date(balance.date && balance.date.substring(0, 7));
        date.setMonth(date.getMonth() + i);

        const monthlyIncome = income.filter(
          (item) =>
            new Date(item.date_of_payment).getMonth() === date.getMonth() && projectstatusfilter.includes(item.project.status.order) 
        );

        return {
          date: date.toLocaleDateString("de-DE", {
            year: "2-digit",
            month: "short",
          }),
          income:
            monthlyIncome.length !== 0
              ? monthlyIncome.map((item) => item.amount).reduce(reducer)
              : 0,
          costs: sumOfCharges,
          invoices: monthlyIncome.length !== 0 ? monthlyIncome : [],
        };
      });

      let returndata = []

      data.forEach((item, i, arr) => {

        let newbal = 0


        if (i === 0) {
          newbal = parseFloat(balance.amount)
        } else {
          newbal = returndata[i-1].balance - arr[i].costs + arr[i].income
        }

        returndata[i] = {
          ...item,
          balance: newbal
        }
      })


      setIncomeData(returndata);
    }
  }, [income, aussicht, charges, balance]);

  useEffect(() => {
    queryCharges();
    queryBalance();
    queryInvoices();
  }, []);


  useEffect(() => {
    setGraphWidth(radRef.current.clientWidth);

    const handleResize = () => {
      setGraphWidth(radRef.current.clientWidth);
    };

    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  const SegmentStyle = {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  };

  return (
    <Grid stackable columns={2}>
      <Grid.Row stretched>
        <Grid.Column width={14}>
          <Segment style={{ height: "400px" }}>
            <TestChart data={incomeData} ref={foreCastRef} />
          </Segment>
        </Grid.Column>
        <Grid.Column width={2}>
          <Segment style={SegmentStyle}>
            <ExcelExport data={incomeData}/>
          </Segment>
          <Segment style={SegmentStyle}>
            <ExcelCosts data={charges}/>
          </Segment>
          <Segment style={SegmentStyle}>
          <Button onClick={()=>svgDownload(foreCastRef.current.current, "Forecast")} icon="area graph" label="Graph"/>
          </Segment>
          
            
        </Grid.Column>
      </Grid.Row>
      <Grid.Row stretched>

        <Grid.Column width={5}>
          <div ref={radRef}>
            <Segment style={{ padding: 0 }}>
              <RadChart
                data={charges}
                graphwidth={graphWidth}
                graphheight={370}
              />
            </Segment>
          </div>
        </Grid.Column>
        <Grid.Column width={4}>
          <Segment style={SegmentStyle}>
          <Checkbox label='Projekte in Aussicht' checked={aussicht} onChange={()=> setAussicht(!aussicht)} />
          </Segment>
          <Segment style={SegmentStyle}>
            <Statistic size="tiny">
              <Statistic.Value>
                {parseFloat(balance.amount).toLocaleString("de-DE", {
                  style: "currency",
                  currency: "EUR",
                  maximumFractionDigits: 0,
                })}
              </Statistic.Value>
              <Statistic.Label>
                {new Date(balance.date).toLocaleDateString("de-DE", {
                  year: "2-digit",
                  month: "long",
                })}
              </Statistic.Label>
            </Statistic>
          </Segment>
          <Segment style={SegmentStyle}>
            <Statistic size="tiny" color={(incomeData.length !== 0 && (parseFloat(incomeData[6].balance - balance.amount) >= 0) ? "green" : "red") }>
              <Statistic.Value>
                {incomeData.length !== 0 &&
                  parseFloat(
                    incomeData[6].balance - balance.amount
                  ).toLocaleString("de-DE", {
                    style: "currency",
                    currency: "EUR",
                    maximumFractionDigits: 0,
                  })}
              </Statistic.Value>
              <Statistic.Label>∆ 6 Monate</Statistic.Label>
            </Statistic>
          </Segment>
          <Segment style={SegmentStyle}>
            <Statistic size="tiny" color={(incomeData.length !== 0 && (parseFloat(incomeData[11].balance - balance.amount) >= 0 )? "green" : "red") }>
              <Statistic.Value>
                {incomeData.length !== 0 &&
                  parseFloat(
                    incomeData[11].balance - balance.amount
                  ).toLocaleString("de-DE", {
                    style: "currency",
                    currency: "EUR",
                    maximumFractionDigits: 0,
                  })}
              </Statistic.Value>
              <Statistic.Label>∆ 12 Monate</Statistic.Label>
            </Statistic>
          </Segment>
        </Grid.Column>
      </Grid.Row>
    </Grid>
  );
}

export default ForecastScreen;
