import { saveAs } from "file-saver";

const dlGraph = (reference, name) => {
    const graph = document.querySelector(".recharts-surface");
    const childs = reference.children
    const svg = childs[0].children
    console.log(svg[0])
    let svgURL = new XMLSerializer().serializeToString(reference);
    let svgBlob = new Blob([svgURL], { type: "image/svg+xml;charset=utf-8" });
    saveAs(svgBlob, name);
}

export default dlGraph